<template>
  <b-row class="mt-5 justify-content-center">
    <b-col md="5">
      <b-card
        header="會員登入"
        header-text-variant="dark"
        header-tag="header"
        header-bg-variant="muted"
        title=""
      >
        <div class="col-9 mx-auto">
          <b-form @submit="onSubmit">
            <b-input-group class="mt-3">
              <b-input-group-prepend is-text
                ><font-awesome-icon :icon="['far', 'user']"
              /></b-input-group-prepend>
              <b-form-input
                size="lg"
                v-model="form.name"
                id="input-name"
                required
                placeholder="請輸入您的帳號"
              ></b-form-input>
            </b-input-group>

            <b-input-group class="mt-3">
              <b-input-group-prepend is-text
                ><font-awesome-icon :icon="['fas', 'lock']"
              /></b-input-group-prepend>
              <b-form-input
                size="lg"
                v-model="form.password"
                id="input-password"
                required
                placeholder="請輸入您的密碼"
              ></b-form-input>
            </b-input-group>

            <b-input-group class="mt-3">
              <b-input-group-prepend is-text
                ><font-awesome-icon :icon="['far', 'keyboard']"
              /></b-input-group-prepend>
              <b-form-input
                size="lg"
                v-model="form.code"
                id="input-code"
                required
                placeholder="請輸入驗證碼4碼"
              ></b-form-input>
            </b-input-group>

            <div class="mt-3">
              <img :src="captcha" alt class="mr-3 img-fluid" />
              <a href="javascript:void(0)" @click="refreshCaptcha">
                <font-awesome-icon :icon="['fas', 'sync-alt']" size="lg" />
              </a>
              <input type="hidden" name="uuid" :value="uuid" />
            </div>
            <b-button block type="submit" class="mt-3 mb-5" variant="success"
              >登入
            </b-button>
          </b-form>
        </div>
      </b-card>
    </b-col>

    <!-- <b-modal ref="msg-modal" hide-footer title="Demo Site">
      <div class="d-block text-center">
        <h4>{{ msg }}</h4>
      </div>
    </b-modal> -->
  </b-row>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faUser, faKeyboard } from "@fortawesome/free-regular-svg-icons";
import { faLock, faRedo, faSyncAlt } from "@fortawesome/free-solid-svg-icons";
import store from "@/store";
import { mapGetters } from "vuex";

library.add(faUser, faLock, faKeyboard, faRedo, faSyncAlt);

export default {
  name: "Login",
  data() {
    return {
      form: {
        name: "",
        password: "",
        code: "",
      },
      // msg: null,
    };
  },
  components: {},
  mounted() {
    console.log("redirect", this.$route.query.redirect);
  },
  beforeRouteEnter(to, from, next) {
    store
      .dispatch("auth/getCaptcha")
      .then((response) => {
        console.log("response", response);
        next();
      })
      .catch((err) => {
        console.log("err", err);
        // alert(err);
        store.dispatch("modal/setModalMsg", err);
        store.dispatch("modal/setModalShow", true);
      });
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();
      console.log(JSON.stringify(this.form));
      this.$store
        .dispatch("auth/login", this.form)
        .then((response) => {
          console.log("response", response);
          if (this.$route.query.redirect) {
            this.$router
              .push({ path: this.$route.query.redirect })
              .catch((err) => console.log("Login.vue", err));
          } else {
            this.$router.push({ name: "index" });
          }
        })
        // wrong //.catch((msg, refresh) => {
        // .catch((msg) => {
        .catch((err) => {
          // let refresh;
          // if(typeof msg === 'object') {
          //   this.msg = msg.msg;
          //   refresh = msg.refresh;
          // } else {
          //   this.msg = msg;
          // }
          console.log("Login.vue auth/login err", err);
          // this.msg = err.message;
          // this.$refs["msg-modal"].show();
          this.$store.dispatch("modal/setModalMsg", err.message);
          this.$store.dispatch("modal/setModalShow", true);
          console.log("refresh", err.refresh);
          if (err.refresh) {
            this.refreshCaptcha();
            // this.refreshCaptcha(event);
            this.form.code = "";
          }
        });
    },
    // refreshCaptcha(event) {
    refreshCaptcha() {
      // event.preventDefault();
      console.log("refreshCaptcha");
      this.$store
        .dispatch("auth/refreshCaptcha")
        .then((response) => {
          console.log("response", response);
        })
        .catch((err) => {
          // this.msg = err;
          // this.$refs["msg-modal"].show();
          this.$store.dispatch("modal/setModalMsg", err);
          this.$store.dispatch("modal/setModalShow", true);
        });
    },
  },
  computed: {
    ...mapGetters("auth", ["uuid", "captcha"]),
  },
};
</script>